import Editor from "@monaco-editor/react";

export default function CodeEditor({ code, setCode, disabled }) {
  return (
    <div className="border rounded-md overflow-hidden">
      <Editor
        height="400px"
        language="python"
        theme="vs-dark"
        value={code}
        onChange={(value) => setCode(value)}
        options={{
          readOnly: disabled,
          minimap: { enabled: false },
        }}
        onMount={(editor) => {
          editor.onPaste((e) => e.preventDefault());
          editor.onKeyDown((e) => {
            if ((e.ctrlKey || e.metaKey) && (e.keyCode === 67 || e.keyCode === 86 || e.keyCode === 88)) {
              e.preventDefault();
            }
          });
        }}
      />
    </div>
  );
}
